<style> 
  .car img { pointer-events: none; }
  .car{
    padding:3vw 2.2vw 20vh;
  }
  .car .collect{
    position: fixed;
    bottom: 55px;
    left: 0px;
    border: 1px solid red;
    width: 100%;
    height: 11vw;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    font-size: 4vw;
  }
  .car .collect>p:first-child{
    width: 70%;
    margin: 0px;
    color: red;
    height: 100%;
    line-height: 11vw;
    text-align: left;
    padding-left: 4vw;
    background: white;
  }
  .car .collect>p:last-child{
    margin: 0;
    width: 30%;
    background: red;
    height: 100%;
    line-height: 11vw;
    color: white;
  }
  .car .card{
    width: 100%;
    height: 30vw;
    margin-bottom: 10px;
    border: 1px solid red;
    border-radius: 5px;
    display: flex;
    padding: 6px;
    box-sizing: border-box;
    position: relative;
  }
  .car .card>.left{
    width: 30%;
    text-align: left;
  }
  .car .card>.right{
    width: 70%;
    padding-left: 5px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }
  .car .card>.left>img{
    width: 100%;
  }
  .car .card>.right>p{
    text-align: left;
    margin: 0;
    font-size: 4vw;
    line-height: 110%;
  }
  .car .card>.right>p:nth-child(2){
    color: red;
    font-size: 5.5vw;
    margin-top: 1vh;
  }
  .car .card>.right>p:nth-child(2)>del{
    color: rgb(211, 211, 211);
    font-size: 4vw;
  }
  .car .card>.right>p:nth-child(3){
    display: flex;
    justify-content: space-between;
    padding-right: 10%;
  }
  .car .card>.right>p:nth-child(3)>span{
    color: rgb(143, 143, 143);
  }
  .car .card>div:last-child{
    color: rgb(150, 150, 150);
    transform: rotate(-90deg) ;
    position: absolute;
    right: 8px;
    top: 45%;
  }
</style>
<template>
  <div class="car">
    <div class="card" v-for="(item,i) of arr" :key="i">
      <div class="left">
        <input type="checkbox" v-model="item.status">
        <img src="../assets/images/1.jpg" alt="">
      </div>
      <div class="right">
        <p>vue+vuex+vue-router+mint-ui 开源demo（简单粗暴）欢迎学习与交流，最好点个赞</p>
        <p>￥{{item.price.toFixed(2)}} <del>￥1888.88</del></p>
        <p>鞋码：{{item.size}} <span>x{{item.count}}双</span></p>
      </div>
      <div>v</div>
    </div>
    <div class="collect">
      <p>订单总额：￥{{price.toFixed(2)}}</p>
      <p @click="jumpOrder">生成订单</p>
    </div>
  </div>
</template>

<script>
import {Toast} from 'mint-ui'
export default {
  data(){
    return {
      arr: [],
      selectIndex:[]
    }
  },
  created(){
    this.arr = this.$store.state.arr.reverse()
  },
  computed:{
    price(){
      this.selectIndex = []
      return this.arr.reduce((prev,elem,i) => {
        if (elem.status) {
          prev+=elem.price*elem.count
          this.selectIndex.push(i)
        }
        return prev
      },0)
    }
  },
  methods:{
    jumpOrder(){
      if(this.selectIndex.length == 0){
        Toast("请选定商品")
        return;
      }
      this.$router.push({name: "我的订单", params:{selectIndex: this.selectIndex}})
    }
  },
  watch:{
    
  }
}
</script>
